import Vue from 'vue';
import Router from 'vue-router';
import Login from '../components/page/Login.vue';
import Register from '../components/page/Register.vue';
import MessageBoard from '../components/page/MessageBoard.vue';
import MessageDetail from "../components/page/MessageDetail";
import Information from "../components/page/Information";
import ManagerMessage from "../components/page/ManagerMessage";
import ResetPassword from "../components/page/ResetPassword";
import UserManage from "../components/page/UserManage";

Vue.use(Router);

const router = new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      component: Login,
    },
    {
      path: '/login',
      component: Login,
      meta: {
        title: '',
      },
    },
    {
      path: '/register',
      component: Register,
      meta: {
        title: '',
      },
    },
    {
      path: '/resetPassword',
      component: ResetPassword,
      meta: {
        title: '',
      },
    },
    {
      path: '/messageBoard',
      component: MessageBoard,
      meta: {
        title: '',
      },
    },
    {
      path: '/managerMessage',
      component: ManagerMessage,
      meta: {
        title: '',
      },
    },
    {
      path: '/messageDetail',
      component: MessageDetail,
      meta: {
        title: '',
      },
    },
    {
      path: '/information',
      component: Information,
      meta: {
        title: '',
      },
    },
    {
      path: '/userManage',
      component: UserManage,
      meta: {
        title: '',
      },
    },
  ],
});
router.beforeEach((to, from, next) => { // beforeEachrouter
  if (to.meta.title) { // 
    document.title = to.meta.title;
  }
  next();
});
export default router;
