<template>
    <el-container class="container">
        <el-header style="">
            <v-head></v-head>
        </el-header>
        <el-container>
            <!--  -->
            <el-main v-if="information">
                <el-card :data="information" class="informationCard">
                    <div slot="header">
                        <span style="font-size: 36px; padding-left: 10px"></span>
                    </div>
                    <div class="information">
                        <label style="width: 600px"></label>
                        <span style="width: 90%;float: right">
                            {{information.username}}
                        </span>
                        <el-divider></el-divider>
                        <label style="width: 200px"></label>
                        <span style="width: 90%;float: right">
                            {{information.nickname}}
                        </span>
                        <el-divider></el-divider>
                        <div style="display:flex;height: 100px;">
                            <label style="width: 10%"></label>
                            <el-image
                                    style="height:100px;width:100px;float: left;"
                                    :src="this.$url + information.avatar"
                                    :preview-src-list="[this.$url + information.avatar]"
                                    fit="fit"></el-image>
                        </div>
                        <el-divider></el-divider>
                        <label style="width: 200px"></label>
                        <span style="width: 90%;float: right">
                            {{information.email}}
                        </span>
                        <el-divider></el-divider>
                        <label style="width: 200px"></label>
                        <span style="width: 90%;float: right" v-if="information.sex === 0"></span>
                        <span style="width: 90%;float: right" v-if="information.sex === 1"></span>
                        <el-divider></el-divider>
                        <label style="width: 200px"></label>
                        <span v-if="information.age!==0" style="width: 90%;float: right">
                            {{information.age}}
                        </span>
                        <span v-if="information.age===0" style="width: 90%;float: right">
                            
                        </span>
                        <el-divider></el-divider>
                        <label style="width: 200px"></label>
                        <span style="width: 90%;float: right">
                            {{information.intro}}
                        </span>
                        <el-divider></el-divider>
                        <label style="width: 200px"></label>
                        <span style="width: 90%;float: right">
                            {{dateFormat(information.createTime)}}
                        </span>
                        <el-divider></el-divider>
                        <div style="float: bottom">
                            <el-button style="float: right;margin-left: 30px;" icon="el-icon-edit" type="primary"
                                       @click="passwordFormVisible = true">
                            </el-button>
                            <el-button style="float: right" icon="el-icon-edit" type="primary"
                                       @click="informationFormVisible = true">
                            </el-button>
                        </div>
                    </div>
                </el-card>
                <el-dialog :close-on-click-modal="false" v-if="informationForm" title=""
                           :visible.sync="informationFormVisible">
                    <el-form :model="informationForm" status-icon :rules="rules" ref="putInformationForm">
                        <el-form-item label="" label-width="20%" prop="nickname">
                            <el-input class="informationTxt" type="text" v-model="informationForm.nickname">
                                {{information.nickname}}
                            </el-input>
                        </el-form-item>
                        <el-form-item label="" label-width="20%" prop="avatar">
                            <el-upload
                                    class="avatar-uploader"
                                    :action="upload()"
                                    :show-file-list="false"
                                    :on-success="handleAvatarSuccess">
                                <img v-if="informationForm.avatar" :src="this.$url+information.avatar" class="avatar">
                                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                            </el-upload>
                        </el-form-item>
                        <el-form-item label="" label-width="20%" prop="email">
                            <el-input class="informationTxt" type="text" v-model="informationForm.email">
                                {{information.email}}
                            </el-input>
                        </el-form-item>
                        <el-form-item label="" label-width="20%" prop="sex">
                            <el-select v-model="informationForm.sex" value="" placeholder="">
                                <el-option key="1" label="" :value="1"></el-option>
                                <el-option key="0" label="" :value="0"></el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="" label-width="20%" prop="age">
                            <el-input class="informationTxt" type="number" v-model="informationForm.age">
                                {{information.age}}
                            </el-input>
                        </el-form-item>
                        <el-form-item label="" label-width="20%" prop="intro">
                            <el-input class="informationTxt" type="textarea" v-model="informationForm.intro">
                                {{information.intro}}
                            </el-input>
                        </el-form-item>
                    </el-form>
                    <div slot="footer" class="dialog-footer">
                        <el-button type="primary" @click="putInformation('putInformationForm')"> </el-button>
                        <el-button @click="informationFormVisible = false"> </el-button>
                    </div>
                </el-dialog>
                <el-dialog :close-on-click-modal="false" title="" :visible.sync="passwordFormVisible">
                    <el-form :model="informationForm" status-icon :rules="rules" ref="putPasswordForm">
                        <el-form-item label="" label-width="20%" prop="oldPassword">
                            <el-input class="informationTxt" type="password" v-model="informationForm.oldPassword">
                            </el-input>
                        </el-form-item>
                        <el-form-item label="" label-width="20%" prop="password">
                            <el-input class="informationTxt" type="password" v-model="informationForm.password">
                            </el-input>
                        </el-form-item>
                        <el-form-item label="" label-width="20%" prop="rePassword">
                            <el-input class="informationTxt" type="password" v-model="informationForm.rePassword">
                            </el-input>
                        </el-form-item>
                    </el-form>
                    <div slot="footer" class="dialog-footer">
                        <el-button type="primary" @click="putInformation('putPasswordForm')"></el-button>
                        <el-button @click="passwordFormVisible = false"> </el-button>
                    </div>
                </el-dialog>
            </el-main>
        </el-container>
    </el-container>

</template>

<script>
    import vHead from '../common/Header.vue';
    import moment from 'moment';
    import {validatePassword, validateUsername, validateEmail} from '@/utils/validate.js';

    export default {
        name: "Information",
        created() {
            this.init();
        },
        data() {
            const validateRePassword = (rule, value, callback) => {
                if (this.informationForm.password !== this.informationForm.rePassword) {
                    callback(new Error(''));
                }
                callback();
            };
            return {
                passwordFormVisible: false,
                information: this.information,
                informationForm: this.informationForm,
                rules: {
                    nickname: [
                        {required: true, message: '', trigger: 'blur'},
                        {
                            min: 3, max: 18, message: '3-18', trigger: 'blur',
                        },
                        {validator: validateUsername, trigger: 'blur'},
                    ],
                    email: [
                        {required: true, message: '', trigger: 'blur'},
                        {validator: validateEmail, trigger: 'blur'},
                    ],
                    sex: [
                        {required: true, message: '', trigger: 'blur'},
                    ],
                    age: [
                        {
                            pattern: /^(0|[1-9]\d?|120)$/,
                            message: '0-120',
                            trigger: 'blur'
                        }
                    ],
                    password: [
                        {required: true, message: '', trigger: 'blur'},
                        {
                            min: 6, max: 24, message: '6-24', trigger: 'blur',
                        },
                        {validator: validatePassword, trigger: 'blur'},
                    ],
                    oldPassword: [
                        {required: true, message: '', trigger: 'blur'},
                        {
                            min: 6, max: 24, message: '6-24', trigger: 'blur',
                        },
                        {validator: validatePassword, trigger: 'blur'},
                    ],
                    rePassword: [
                        {required: true, message: '', trigger: 'blur'},
                        {validator: validateRePassword, trigger: 'blur'},
                    ],
                },
                informationFormVisible: false,
            }
        },
        components: {
            vHead,
        },
        methods: {
            upload(file) {
                let fd = new FormData();//form
                fd.append("picFile", file); //
                this.$http
                    .post('/api/avatar/upload', fd)
                    .then(res => {
                        console.log(res.data);
                        let data = res.data;
                        if (data.code == 200) {
                            this.$message({
                                message: "",
                                type: "success"
                            });
                        } else {
                            this.$message.error(data.msg);
                        }
                    })
                    .catch(error => {
                        // console.log(error);
                    });
            },
            init() {
                this.$http.get('/api/information/')
                    .then((res) => {
                        const data = res.data;
                        this.information = data.data;
                        this.informationForm = {
                            nickname: this.information.nickname,
                            email: this.information.email,
                            avatar: this.information.avatar,
                            sex: this.information.sex,
                            intro: this.information.intro,
                            age: this.information.age,
                        };
                    });

            },
            dateFormat: function (date) {
                if (date === undefined) {
                    return "";
                }
                moment.locale('zh-cn');
                return moment(date).format('llll');
            },
            putInformation(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.$http.put('/api/information', this.informationForm)
                            .then((res) => {
                                const result = res.data;
                                if (result.code === 0) {
                                    this.$message.success(result.msg);
                                    // 
                                    this.init();
                                    // 
                                    this.$refs[formName].resetFields();
                                } else {
                                    this.$message.error(result.msg);
                                }
                            })
                            .catch((err) => {
                                console.log(err);
                            });
                    } else {
                        return false;
                    }
                });
                this.informationFormVisible = false
            },
            handleAvatarSuccess(res, file) {
                this.informationForm.avatar = URL.createObjectURL(file.raw);
            },
            beforeAvatarUpload(file) {
                const isJPG = file.type === 'image/jpeg';
                const isLt5M = file.size / 1024 / 1024 < 5;
                if (!isJPG) {
                    this.$message.error(' JPG !');
                }
                if (!isLt5M) {
                    this.$message.error(' 2MB!');
                }
                return isJPG && isLt5M;
            },
        },
    }
</script>

<style scoped>
    .container {
        height: 100%;
        width: 80%;
        margin: auto;
    }

    .informationCard {
        height: 92%;
        width: 100%;
    }

    .information {
        margin: 20px;
    }

    .informationTxt {
        width: 80%;
    }

    .informationTxt >>> .el-textarea__inner {
        resize: none;
        max-height: 300px;
        height: 100px;
    }

    .avatar-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }

    .avatar-uploader .el-upload:hover {
        border-color: #409EFF;
    }

    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 178px;
        height: 178px;
        line-height: 178px;
        text-align: center;
    }

    .avatar {
        width: 100px;
        height: 100px;
        display: block;
    }

</style>
