'use strict';
const validatePassword = (rule, value, callback) => {
    if (!(/^[^\s]*$/.test(value))) {
        callback(new Error(''));
    }
    callback();
};
const validateUsername = (rule, value, callback) => {
    if (!(/^[^\s]*$/.test(value))) {
        callback(new Error(''));
    }
    callback();
};

const validateEmail = (rule, value, callback) => {
    if (!(/^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+((.[a-zA-Z0-9_-]{2,3}){1,2})$/.test(value))) {
        callback(new Error(''));
    }
    callback();
};
export {
    validatePassword,
    validateUsername,
    validateEmail,
}
